	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/creating-an-icon-with-photoshop/index.md absPath of file >>> MarkdownRemark","html":"<p>I just upgraded to CS3 (finally!), yet I'm still dissappointed by the lack of support for Icon files. Especially now that favicons are so in vogue now. Fear not though, because Open-source plugins are to the rescue!</p>\n<p>Very easily, you can now save out both normal and package icons from Photoshop, with the addition of the plugins available from <a href=\"http://www.telegraphics.com.au/sw/\" title=\"Open link in a new window\">Telegraphics</a>.</p>\n<p>It's about time I had a proper favicon for this site, so I stole some inspiration from Adobe and added my own little twist. Voila!</p>\n<p><img src=\"/portfolio-2018/icon-2fbfe9bd8d0497fb3ea301a1df4e3c8b.gif\" alt=\"icon.gif\"></p>","frontmatter":{"title":"Creating an Icon with Photoshop","date":"July 21, 2007","tags":["open-source","photoshop","photoshop-resources","resources"],"url":null}}},"pathContext":{}}

